@import "global.scss";

.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: calc(100vh - 70px);
    background-color: $mainColor;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scrollbar-width: none; //for firefox
    @include mobile {
      scroll-snap-type: none;
      width: 100%;
      height: calc(100vh - 90px);
      background-color: $mainColor;
      position: relative;
      top: 30px;
      scroll-behavior: smooth;
      scrollbar-width: none; //for firefox
    }
    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}
